@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@600&display=swap);
.cabecalho {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  background-color: var(--chumbo);
  width: 100%;
}

.cabecalho__titulo {
  text-align: center;
  color: var(--branco);
  font-size: 4rem;
  font-family: 'Pacifico', cursive;
  margin: 2rem;
}

.cabecalho__titulo span {
  color: var(--vermelho);
}


.formulario {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  min-width: auto;
  max-width: 450px;
  margin: 0 auto;
  padding: 1rem;
}

.formulario__titulo, .formulario__texto, .formulario__botao {
  margin: 1rem 0;
  border-radius: 1rem;
  width: 100%;
}

.formulario__titulo, .formulario__texto {
  background-color: var(--cinza-claro);
  border: .2rem dashed var(--chumbo);
  padding: 1rem;
  outline: none;
}

.formulario input::-webkit-input-placeholder, .formulario textarea::-webkit-input-placeholder {
  color: var(--chumbo);
}

.formulario input:-ms-input-placeholder, .formulario textarea:-ms-input-placeholder {
  color: var(--chumbo);
}

.formulario input::placeholder, .formulario textarea::placeholder {
  color: var(--chumbo);
}

.formulario__titulo {
  font-size: 2rem;
  font-family: 'Pacifico', cursive;
  text-align: center;
}

.formulario__texto {
  height: 16rem;
  font-size: 1.2rem;
  font-family: 'Nunito Sans', sans-serif;
  resize: none;
}

.formulario__botao {
  background-color: var(--vermelho);
  color: var(--branco);
  border: 0;
  font-size: 2rem;
  font-family: 'Pacifico', cursive;
  outline: none;
  transition: 500ms;
}

.formulario__botao:hover {
  background-color: var(--vermelho-escuro);
}
.nota {
  background-color: var(--branco);
  display: grid;
  grid-template-rows: 30% 10% 50% 10%;
  margin: 1rem;
  width: 300px;
  height: 28rem;
  -webkit-align-items: center;
          align-items: center;
}

.nota__titulo, .nota__texto {
  color: var(--chumbo);
  padding: 1rem;
  word-break: break-all;
}

.nota__titulo {
  text-align: center;
  font-size: 2rem;
  font-family: 'Pacifico', cursive;
}

.nota__divisoria {
  background-color: var(--chumbo);
  height: 2px;
  margin: 1rem;
}

.nota__texto {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 1.2rem;
  height: 100%;
}

.nota__icone {
  fill: var(--chumbo);
  margin: 0 auto;
}

.lista-de-notas {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  background-color: var(--chumbo);
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}
.rodape {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: var(--vermelho);
}

.rodape__informacao, .rodape__icone {
  margin: .2rem 1rem;
}

.rodape__informacao {
  color: var(--branco);
  font: 1rem 'Nunito Sans', sans-serif;  
}

.rodape__icone {
  fill: var(--branco);
}
.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
	box-sizing: border-box;
}

:root {
	--chumbo: #222831;
	--branco: #eeeeee;
	--cinza-claro: #dfdfdf;
	--vermelho: #f52f57;
	--vermelho-escuro: #f41542;
	--azul: #1ac8db;
	--amarelo: #f9d030;
	--verde: #b8ee30;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

